@import url(https://fonts.googleapis.com/css2?family=Mulish:wght@200;300;400;500;600;700&display=swap);
@font-face{font-family:'DIN Alternate';src:url(/mindfulness-prompts/static/media/DINAlternate-Bold.11cf96e5.woff) format("Woff")}*{margin:0;padding:0}body{background-image:url(/mindfulness-prompts/static/media/backdrop.7b108a45.svg);background-attachment:fixed;background-size:cover;background-position:center;font-family:"DIN Alternate",sans-serif}h1{color:#41a3b5;font-size:2.3rem;font-weight:500;text-align:center;margin-top:14%}button{border:none;color:white;font-family:"DIN Alternate",sans-serif;font-size:1.4rem;background-color:#69bac9;margin:20px auto;padding:13px;width:100%;border-radius:8px;box-shadow:0px 4px #4295a5}button:hover{-webkit-transform:translate(0, -1px);transform:translate(0, -1px);box-shadow:0px 5px #4295a5}button:active{-webkit-transform:translate(0, 2px);transform:translate(0, 2px);box-shadow:0px 2px #4295a5}.card{border-radius:6px;box-shadow:rgba(0,0,0,0.24) 0px 3px 8px;background-color:white}.promptArea{margin:5% 5% 0;padding:10px 30px;margin-top:20%;max-width:400px}.promptText{height:130px;text-align:center;align-content:center;margin:0, auto}.promptText .prompt{top:50%;-webkit-transform:translateY(-50%);transform:translateY(-50%);font-size:1.8rem;color:#7b7b7b}.promptText .prompt div{min-width:100%}.ReactModal__Overlay{opacity:0;transition:opacity 300ms ease-out}.ReactModal__Overlay--after-open{opacity:1}.ReactModal__Overlay--before-close{opacity:0}.ReactModal__Content{position:absolute;top:40px;left:40px;right:40px;bottom:40px;overflow:auto;-webkit-overflow-scrolling:touch;outline:none;padding:20px;inset:10px;max-height:660px;max-width:500px;margin:auto;-webkit-transform:translateY(90%);transform:translateY(90%);transition:-webkit-transform 300ms cubic-bezier(0.175, 0.885, 0.32, 1.275);transition:transform 300ms cubic-bezier(0.175, 0.885, 0.32, 1.275);transition:transform 300ms cubic-bezier(0.175, 0.885, 0.32, 1.275), -webkit-transform 300ms cubic-bezier(0.175, 0.885, 0.32, 1.275)}.ReactModal__Content ul{font-size:1.5rem;list-style:none;margin-bottom:40px}.ReactModal__Content ul li{margin:12px 0}.ReactModal__Content button{margin:0}.ReactModal__Content .modalScroll{height:90%;overflow:scroll;margin-bottom:15px}.ReactModal__Content--after-open{-webkit-transform:translateY(0);transform:translateY(0)}.ReactModal__Content--before-close{-webkit-transform:translateY(90%);transform:translateY(90%)}footer{position:absolute;bottom:0;background:white;width:100%;text-align:center;padding:20px 0}footer p{font-size:1.2rem}@media screen and (min-width: 512px){.promptArea{margin:100px auto 0}}@media screen and (min-width: 640px){h1{margin-top:80px;font-size:2.8rem}}@media screen and (max-height: 660px){footer{position:revert;bottom:none;background:none;padding:0;max-height:30px}.promptArea{margin-bottom:30px}}

